<script>
// import {axiosInstance} from './../axios';
import axios from 'axios';
import xxx from './mdj'

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data(){
    return {
      reshu:[],
      flag:true
    }
  },  
  mounted(){
    console.log('xxx',xxx);
    const arr = xxx.map(res=> res.CODE);
    // console.log('arr',arr.slice(0,10));
    // arr.slice(0,10)

   //  arr.slice(0,20)
    // ['10000601','10000803']
    //arr.
    arr.forEach( res=>{
      setTimeout(()=> {
        if(this.flag) this.getRS(res)
      },100)
      
    })
    // axios.post('?').then(()=>{

    // })
  },
  methods:{
    async getRS(zwdm){  
      let that = this;
      await axios({
    method: 'post',
    url: 'https://www.hljsgwy.org.cn/tyzpwb/stuchooseexam/getPositionInfo.htm',
    data: {
      // dsdm:12300,
      // userid:'5b335c38607a6139',
      'zwdm':zwdm,
      examid:'f10389e9c3c89250'
    },
    headers: {'Content-Type': 'multipart/form-data' }
    })
    .then(function (response) {
        //handle success
        console.log('response',response.data);
        if(!response.data){
          that.flag = false
          setTimeout(()=>{
            that.flag = true
          },1000)
        }
        response.data.xszwdm && that.reshu.push({
          name: `${response.data.zpdwmc1}  ${response.data.xszwdm ? response.data.xszwdm + '岗' :''} `,
          bmrs: response.data.bkrs
        })
    })
    .catch(function (response) {
        //handle error
        console.log(response);
    });          
    }    
  }
}
</script>

