  computed:{
    priceColor(){
      if(this.priceType === 'up') return 'color: green'
      else if(this.priceType === '---') return 'color: gray'
      else if(this.priceType === 'down') return 'color: red'
      else return 'color: gray'
    }
  },
  watch:{
    BTCUSDT(_new,_old) {
      if(_new > _old) {
        this.priceType = 'up';
      } else if(_new === _old) {
        this.priceType = '---';
      } else {
        this.priceType = 'down';
      }
    }
