    socket.onmessage = function(msg) { //監聽訊息(onmessage)
      console.log('msg', msg)
      _this.BTCUSDT = JSON.parse(msg.data).result.price
      if( _this.BTCUSDT ) {
        setTimeout(()=>{
          send()
        }, 1000)
      } else{ console.log("msg er", msg)}
